<template>
  <div id="app">
    <el-form :inline="true" :model="searchForm" ref="searchForm">
      <el-form-item label="" prop="name">
        <el-input v-model="searchForm.name" placeholder="ID"></el-input>
      </el-form-item>
      <el-form-item label="" prop="status">
        <el-select v-model="searchForm.status" placeholder="">
          <el-option label="" value=""></el-option>
          <el-option label="" value="1"></el-option>
          <el-option label="" value="-1"></el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="" prop="type">
        <el-select v-model="searchForm.type" multiple placeholder="">
          <el-option label="" value="1"></el-option>
          <el-option label="" value="2"></el-option>
          <el-option label="UPI" value="3"></el-option>
          <el-option label="PIX" value="4"></el-option>
          <el-option label="CLABE" value="4"></el-option>
          <el-option label="HTML" value="7"></el-option>
        </el-select>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="search"></el-button>
        <el-button @click="resetForm('searchForm')"></el-button>
      </el-form-item>
    </el-form>
    <el-row>
      <el-button plain @click="dialog.visible=true"></el-button>
    </el-row>
    <el-table
      :row-class-name="tableRowClassName"
      :data="tableData" size="small"
      style="width: 100%">
      <el-table-column type="expand">
        <template slot-scope="props">
          <el-form label-position="left" inline class="demo-table-expand">
            <el-form-item label="">
              <span>{{ props.row.params }}</span>
            </el-form-item>
          </el-form>
        </template>
      </el-table-column>
      <el-table-column
        prop="id"
        label="ID">
      </el-table-column>
      <el-table-column prop="name" label="">
      </el-table-column>
      <el-table-column prop="outName" label="">
      </el-table-column>
      <el-table-column label="">
        <template slot-scope="scope">
          <el-tag type="success" size="small">{{Number(scope.row.feeFix/100).toFixed(2)}}+{{
            Number(scope.row.feeRate*100).toFixed(2)}}%
          </el-tag>
        </template>
      </el-table-column>

      <el-table-column prop="sort" label="">
      </el-table-column>
      <el-table-column label="">
        <template slot-scope="scope">
          <el-tag v-if="scope.row.types && scope.row.types.indexOf('1')!=-1" size="mini"></el-tag>
          <el-tag type="info" v-if="scope.row.types && scope.row.types.indexOf('2')!=-1" size="mini"></el-tag>
          <el-tag type="success" v-if="scope.row.types && scope.row.types.indexOf('3')!=-1" size="mini">UPI
          </el-tag>
        </template>
      </el-table-column>
      <el-table-column label="" prop="range">
      </el-table-column>
      <el-table-column label="" prop="workTime">
      </el-table-column>
      <el-table-column label="" prop="currency">
      </el-table-column>
      <el-table-column label="" prop="createTime" :formatter="dateFormat">
      </el-table-column>
      <el-table-column label="/">
        <template slot-scope="scope">
          <el-tooltip :content="scope.row.status==1?'':''" placement="top">
            <el-switch
              v-model="scope.row.status"
              active-color="#13ce66"
              inactive-color="#ff4949"
              :active-value="1"
              :inactive-value="-1"
              @change="changeStatus(scope.row)">
            </el-switch>
          </el-tooltip>
        </template>
      </el-table-column>
      <el-table-column label="" width="300">
        <template slot-scope="scope">
          <el-button size="mini" @click="handleEdit(scope.$index, scope.row)"></el-button>
          <el-button size="mini" type="danger" @click="handleDelete(scope.$index, scope.row)"></el-button>
          <el-button size="mini" @click="handlePayTest(scope.$index, scope.row)"></el-button>
        </template>
      </el-table-column>
    </el-table>
    <div style="text-align: center;margin-top:5px;">
      <el-pagination
        background
        layout="total, prev, pager, next"
        :total="page.total" :current-page="page.cur" @current-change="pageChange">
      </el-pagination>
    </div>
    <!--  -->
    <el-dialog title="" :visible.sync="dialog.visible" @closed="closeDialog">
      <el-dialog
        title=""
        :visible.sync="Successdialog"
        :show-close=false
        :close-on-click-modal=true
        :close-on-press-escape=false
        width="30%"
        center>
        <span slot="footer" class="dialog-footer">
        <el-button type="primary" @click="Successdialog = false"></el-button>
      </span>
      </el-dialog>
      <el-form :model="dialog.form" :rules="rules" ref="ruleForm" label-width="150px" class="demo-ruleForm">
        <el-form-item label="" prop="name">
          <el-input v-model="dialog.form.name"></el-input>
        </el-form-item>
        <el-form-item label="" prop="outName">
          <el-input v-model="dialog.form.outName"></el-input>
        </el-form-item>
        <el-form-item label="" prop="params">
          <el-input type="textarea" autosize v-model="dialog.form.params"></el-input>
        </el-form-item>
        <el-form-item label="" prop="feeFix">
          <el-input v-model="dialog.form.feeFix"></el-input>
        </el-form-item>
        <el-form-item label="" prop="feeRate">
          <el-input v-model="dialog.form.feeRate"></el-input>
        </el-form-item>
        <el-form-item label="" prop="sort">
          <el-input-number v-model="dialog.form.sort" :min="0"></el-input-number>
        </el-form-item>
        <el-form-item label="" prop="type">
          <el-checkbox-group v-model="type">
            <el-checkbox label="" name="type"></el-checkbox>
            <el-checkbox label="" name="type"></el-checkbox>
            <el-checkbox label="UPI" name="type">UPI</el-checkbox>
            <el-checkbox label="HTML" name="type">HTML</el-checkbox>
          </el-checkbox-group>
        </el-form-item>
        <el-form-item label="" prop="workTime">
          <el-input v-model="dialog.form.workTime"></el-input>
        </el-form-item>
        <el-form-item label="" prop="range">
          <el-tag type="info" v-if="!dialog.isInsert">{{dialog.form.thirdRange}}</el-tag>
          <el-input v-model="dialog.form.range" placeholder="100-500000"></el-input>
        </el-form-item>
        <el-form-item label="" prop="thirdRange" v-if="dialog.isInsert">
          <el-input v-model="dialog.form.thirdRange" placeholder="100-500000"></el-input>
        </el-form-item>
        <el-form-item label="" prop="currency">
          <el-select v-model="dialog.form.currency" placeholder="">
            <el-option label="INR" value="INR"></el-option>
            <el-option label="MXN" value="MXN"></el-option>
            <el-option label="BRL" value="BRL"></el-option>
            <el-option label="USD" value="USD"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item>
          <el-button size="medium" icon="el-icon-search" type="primary" @click="submitForm('ruleForm')"></el-button>
          <el-button size="medium" icon="el-icon-refresh-left" @click="resetForm('ruleForm')"></el-button>
        </el-form-item>
      </el-form>
    </el-dialog>

    <!--  -->
    <el-dialog title="" :visible.sync="dialogTest.visible">
      <!--            <el-form :model="dialogTest.form" :rules="rulesTest" ref="ruleTestForm" label-width="150px" class="demo-TestForm">-->
      <!--                <el-form-item label="" prop="payUrl">-->
      <!--                    <el-input v-model="dialogTest.form.payUrl"></el-input>-->
      <!--                </el-form-item>-->
      <!--            </el-form>-->
      <el-dialog
        title=""
        :visible.sync="Successdialog"
        :show-close=false
        :close-on-click-modal=false
        :close-on-press-escape=false
        :modal='false'
        width="30%"
        center>
            <span v-if="testPayUrl" style="font-size:16px;"><span
              style="color: #01AAED">{{testPayUrl}}</span></span>
        <span v-if="testPayNumber" style="font-size:16px;">:{{testPayNumber}}</span>
        <span slot="footer" class="dialog-footer">
        <el-button @click="Successdialog = false"></el-button>
        <el-button type="primary" @click="returnPayUrl()"></el-button>
      </span>
      </el-dialog>
      <el-form ref="form" :model="dialogTest.form" label-width="90px">
        <el-form-item label="Id">
          <el-input v-model="dialogTest.form.appId"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="dialogTest.form.amount"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-select v-model="dialogTest.form.type" placeholder="">
            <el-option label="" value="1"></el-option>
            <el-option label="" value="2"></el-option>
            <el-option label="UPI" value="3"></el-option>
            <el-option label="PIX" value="4"></el-option>
            <el-option label="HTML" value="7"></el-option>
          </el-select>
        </el-form-item>
      </el-form>
      <el-button @click="dialogTest.visible = false"> </el-button>
      <el-button type="primary" @click="testPay()"> </el-button>
    </el-dialog>

  </div>
</template>

<script>
  import {addPayType, changePayTypeStatus, deletePayType, queryPayTypePageList, updatePayType} from "../../api/paytype"
  import {dateFormate} from "../../statics/custom";

  export default {
    name: "payTypeList",
    data() {
      let isNumber = (rule, value, callback) => {
        if (typeof value == 'number') {
          callback();
          return;
        }
        if (value == null || value.indexOf(' ') !== -1) {
          callback(new Error('!'));
          return;
        }
        if (!isNaN(value)) {
          callback();
        } else {
          callback(new Error('!'));
        }
      }
      return {
        fullscreenLoading: true,
        tableData: [],
        types: [],
        testPayVisible: false,
        Successdialog: false,
        isDisabled: false,
        Sencond: 10,
        testPayUrl: '',
        testPayNumber: '',
        page: {
          pageSize: 10,
          total: 0,
          cur: 1
        },
        type: [],
        dialog: {
          isInsert: true,
          visible: false,
          form: {
            name: '',
            outName: '',
            params: '',
            feeFix: '',
            feeRate: '',
            sort: '',
            types: '',
            workTime: '',
            range: '',
            currency: '',
            thirdRange: ''
          }
        },
        dialogTest: {
          isInsert: true,
          visible: false,
          form: {
            appId: '',
            amount: '',
            type: '',
            payTypeId: ''
          }
        },
        searchForm: {
          name: '',
          stauts: '',
          type: [],
          types: ''
        },
        rulesTest: {
          Name: [
            {required: true, message: '', trigger: 'blur'},
          ],
          Params: [
            {required: true, message: '', trigger: 'blur'},
          ],
          FeeFix: [
            {validator: isNumber, trigger: 'blur'},
          ],
          FeeRate: [
            {validator: isNumber, trigger: 'blur'},
          ],
          Type: [
            {type: 'array', required: true, message: '', trigger: 'change'}
          ],
          Currency: [
            {required: true, message: '', trigger: 'change'}
          ],
          Range: [
            {required: true, message: '', trigger: 'blur'},
          ],
          ThirdRange: [
            {required: true, message: '', trigger: 'blur'},
          ]
        },
        rules: {
          out_trade_no: [
            {required: true, message: '', trigger: 'blur'},
          ],
          payUrl: [
            {required: true, message: '', trigger: 'blur'},
          ]
        }
      }
    },
    async created() {
      this.refresh()
    },
    methods: {
      refresh() {
        let that = this
        this.searchForm.types = this.type.sort(function (a, b) {
          return a - b
        }).join(',')
        queryPayTypePageList({
          page: this.page.cur,
          limit: this.page.pageSize,
          status: this.searchForm.status,
          name: this.searchForm.name,
          types: this.searchForm.types
        }).then(function (response) {
          that.fullscreenLoading = false
          if (response.data.code === 0) {


            that.tableData = response.data.data
            that.page.total = response.data.count
          }
        })
      },
      dateFormat(row, column) {
        let date = row[column.property];
        if (date === undefined || date == null) {
          return ''
        }
        return dateFormate(date)
      },
      pageChange(cur) {
        this.page.cur = cur
        this.refresh()
      },
      tableRowClassName({row, rowIndex}) {
        if (row.status === 1) {
          return 'success-row'
        } else {
          return 'warning-row'
        }
      },
      handleEdit(index, row) {
        this.dialog.form.id = row.id
        this.dialog.form.name = row.name
        this.dialog.form.outName = row.outName
        this.dialog.form.params = row.params
        this.dialog.form.status = row.status
        this.dialog.form.feeRate = row.feeRate
        this.dialog.form.sort = row.sort
        this.dialog.form.workTime = row.workTime
        this.dialog.form.range = row.range
        this.dialog.form.currency = row.currency
        this.dialog.form.thirdRange = row.thirdRange
        this.dialog.form.types = row.types
        this.type = row.types === '' || row.types === undefined ? [] : row.types.indexOf(',') > 0 ? row.types.replaceAll('1', '').replaceAll('2', '').replaceAll('3', 'UPI').replaceAll('7', 'HTML').split(',') : [row.types]
        this.dialog.form.feeFix = Number(row.feeFix / 100).toFixed(2)
        this.dialog.visible = true
        this.dialog.isInsert = false
      },
      handlePayTest(index, row) {
        if (row.status === 1) {
          let types = row.types.split(',')
          let type = types[0]
          let amountRange = row.range.split('-')
          let amount = amountRange[0]
          let currency = row.currency
          let payTypeId = row.id


          // let payUrl = 'http://localhost:8081/appclient/testPay.do?payTypeId='+payTypeId+'&type='+type+'&amount='+amount*100+'&currency='+currency;
          //let payUrl = 'https://geepay.site/appclient/testPay.do?payTypeId=' + payTypeId + '&type=' + type + '&amount=' + amount * 100 + '&currency=' + currency;

          this.dialogTest.form.payTypeId = payTypeId
          this.dialogTest.visible = true

          // axios({method: 'post',url:'/paytype/testPay.action',params:{
          //         type:type,
          //         amount:amount,
          //         currency:currency,
          //         payTypeId:payTypeId
          //     }}).then(res=>{
          //     if(res.data.code == 0){
          //
          //     }else {
          //         this.$message({
          //             message: res.data.msg,
          //             type: 'warning'
          //         });
          //     }
          // })
        } else {
          this.$message({
            message: '',
            type: 'warning'
          });
        }
      },
      handleDelete(index, row) {
        let that = this
        this.$confirm(', ?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          deletePayType({
            id: row.id
          }).then(function (response) {
            if (response.data.code === 0) {
              that.$message({
                message: '',
                type: 'success'
              })
            } else {
              that.$message.error('');
            }
            that.refresh()
          })
        })
      },
      submitForm(formName) {
        let str = this.dialog.isInsert ? '' : ''
        if (this.dialog.isInsert) {
          delete this.dialog.form.id
        }
        let that = this
        this.$refs[formName].validate((valid) => {
          if (valid) {
            this.$confirm('' + str + '?', '', {
              confirmButtonText: '',
              cancelButtonText: '',
              type: 'warning'
            }).then(() => {
              that.dialog.form.types = that.type.toString().replace('', '1').replace('', '2').replace('UPI', '3').replace('HTML', '7')
              (this.dialog.isInsert ? addPayType(that.dialog.form) : updatePayType(that.dialog.form)
              ).then(function (response) {
                if (response.data.code === 0) {
                  that.$message({
                    message: str + '',
                    type: 'success'
                  })
                } else {
                  that.$message.error(str + '');
                }
                that.type = []
                that.refresh()
                that.closeDialog()
              })
            }).catch(() => {
            })
          } else {
            return false;
          }
        });
      },
      resetForm(formName) {
        this.$refs[formName].resetFields();
      },
      closeDialog() {
        let obj = this.dialog.form
        Object.keys(obj).forEach(key => {
          let type = typeof obj[key]
          if (type === 'number') {
            obj[key] = 0
          }
          if (type === 'string') {
            obj[key] = ''
          }
        })
        obj.stauts = -1
        this.type = []
        this.dialog.visible = false
        this.dialog.isInsert = true
        // this.resetForm('ruleForm')
      },
      search() {
        this.page.cur = 1
        this.refresh()
      },
      testPay() {
        var that = this
        that.testPayUrl = ''
        that.testPayNumber = ''
        axios({
          method: 'post',
          url: 'https://geepay.site/appclient/testPay.do',
          data: 'appId=' + this.dialogTest.form.appId +
            '&amount=' + this.dialogTest.form.amount +
            '&type=' + this.dialogTest.form.type +
            '&payTypeId=' + this.dialogTest.form.payTypeId
        }).then(function (response) {
          var url = response.data
          if (url !== '') {
            if (url.toString().startsWith("http")) {
              that.$message('')
              that.testPayUrl = url
              that.Successdialog = true
              //that.getSencond(url)
            } else {
              that.testPayNumber = url
              that.Successdialog = true
            }
          } else {
            that.$message("")
          }


        }).catch(function (response) {
          console.log(response);//
        })
      },
      getSencond(data) {
        const that = this
        const interval = window.setInterval(function () {
          --that.Sencond
          if (that.Sencond === 0) {
            //that.isDisabled = false
            window.clearInterval(interval)
            window.open(data)//
          }
        }, 1000)
      },
      sendMsg() {
        window.close()
      },
      returnPayUrl() {
        window.open(this.testPayUrl)
      },
      changeStatus(row) {
        let that = this
        changePayTypeStatus({
          id: row.id,
          status: row.status
        }).then(function (response) {
          if (response.data.code === 0) {
            that.$message({
              message: '',
              type: 'success'
            })
          } else {
            that.$message.error('');
          }
          that.refresh()
        })
      },
    }
  }
</script>

<style scoped>

</style>



